body {
  margin: 0;
  padding: 0;
  background: #F3F4F6;
}
.nav{
  background: white;
  padding: 40px;
  overflow: hidden;
}

.nav a{
  float: left;
  display: block;
  text-align: center;
  padding: 14px 20px;
  text-decoration: none;
  color: black;
  font-size: 40px ;
}

.nav img{
  width: 70px;
  height: 70px;
  float: right;
  border-radius: 50%;
}


.column {
  flex: 1;
  padding: 20px;
  background: white;
  margin: 30px;
  border-radius: 5px;

}

.container {
  display: flex;
}

p a {
  text-decoration: none;
 
}

input[type="text"],
input[type="password"]{
  display: block;
  width: 100%;
  padding: 10px;
  margin: 5px;
  border-radius: 8px;
  border: 1px solid rgb(190, 190, 239);
  box-sizing: border-box;
  font-size: 20px;
}

input[type="submit"]{
  margin: 5px;
  background-color: rgb(15, 155, 231);
  border: none;
  padding: 10px 30px 10px 30px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}